body {
  font-family: "Open Sans", sans-serif;
}

a {
  text-decoration: none;
}

h2 {
  font-family: "Raleway", sans-serif;
  font-size: 2.25rem;
}

.container {
  max-width: 1325px;
  margin: 0 auto;
  padding: 0 0.9375rem;
}
.container .row {
  display: flex;
  flex-wrap: wrap;
}

[class*=col-] {
  min-height: 1px;
}
@media only screen and (max-width: 795px) {
  [class*=col-] {
    width: 100%;
  }
}

.header {
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 10;
  height: 70px;
  box-shadow: 0 3px 6px rgba(0, 128, 0, 0.26);
}
.header__container {
  width: 100%;
  height: 100%;
}
.header__container .nav {
  background-color: #F7F7F7;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 2em;
  position: relative;
}
@media only screen and (max-width: 795px) {
  .header__container .nav__link {
    color: #0000ff;
  }
}
.header__container .nav .logo {
  margin-left: 2px;
  display: flex;
  align-items: center;
  width: 30%;
}
.header__container .nav .logo__rigo {
  border-radius: 50%;
  border: solid 2px #818181;
  width: 2.5rem;
  height: 2.5rem;
  background-image: url("../assets/swan-male.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}
.header__container .nav .logo__string {
  font-family: "Raleway", sans-serif;
  font-size: 1.5rem;
  font-weight: 600;
  color: #0000ff;
  margin-left: 2px;
}
.header__container .nav__buttons {
  display: flex;
  width: 55%;
  justify-content: space-around;
}
@media only screen and (max-width: 795px) {
  .header__container .nav__buttons {
    position: absolute;
    right: 0;
    top: 75px;
    background-color: #e1e2e3;
    height: 20vh;
    flex-direction: column;
    text-align: center;
    width: 20%;
    transform: translate(100%);
  }
  .header__container .nav__buttons--active {
    transform: translate(0%);
    z-index: 5;
  }
}
.header__container .nav__element a {
  position: relative;
  padding: 0.2em;
  font-size: 1rem;
  letter-spacing: 2px;
  font-weight: bold;
}
.header__container .nav__element a:hover {
  background-color: #e1e2e3;
  border-radius: 20px;
}
.header__container .nav__element a:after {
  content: "";
  position: absolute;
  bottom: -0.8rem;
  left: 0;
  background-color: #0000ff;
  height: 0.15em;
  border-radius: 10px;
  transition: 0.3s ease-in-out;
}
.header__container .nav__element a:hover::after {
  width: 100%;
}
.header__container .nav__hamburger {
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  display: none;
}
.header__container .nav__hamburger--line {
  width: 3em;
  height: 0.3em;
  background-color: #0000ff;
  margin: 0.7em;
  border-radius: 20px;
  transition: 0.3s ease-in-out;
}
@media only screen and (max-width: 795px) {
  .header__container .nav__hamburger {
    display: block;
    transition: 0.3s ease-in-out;
  }
  .header__container .nav__hamburger--active .nav__hamburger--line:first-child {
    transform: translateY(-999px);
  }
  .header__container .nav__hamburger--active .nav__hamburger--line:nth-child(2) {
    transform: rotate(-405deg) translateY(10px);
  }
  .header__container .nav__hamburger--active .nav__hamburger--line:nth-child(3) {
    transform: rotate(45deg) translateY(-10px);
  }
}

.openning {
  width: 100%;
  max-width: 1325px;
  position: relative;
  max-height: 600px;
}
.openning__Slides {
  width: 100%;
  height: 100%;
  border-radius: 20px;
}
.openning__Button:hover {
  cursor: pointer;
}
.openning__Button--Left {
  position: absolute;
  top: 45%;
  left: 0;
  height: 50px;
  width: 50px;
  background-image: url("../assets/arrow_left.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  border: solid 2px #000000;
  border-radius: 50%;
  text-align: center;
  font-size: large;
  font-weight: bold;
}
.openning__Button--Right {
  position: absolute;
  top: 45%;
  right: 0;
  height: 50px;
  width: 50px;
  background-image: url("../assets/arrow_right.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  border: solid 2px #000000;
  border-radius: 50%;
  text-align: center;
  font-size: large;
  font-weight: bold;
}
.openning__Title {
  width: 75%;
  position: absolute;
  text-align: center;
  top: 18%;
  left: 10%;
  font-size: 800;
  font-weight: bold;
  border: solid 3px #0000ff;
  border-radius: 50px;
  background-color: rgba(0, 128, 0, 0.26);
  color: #0000ff;
}

.offer {
  max-width: 1325px;
  position: relative;
  max-height: 600px;
  border: 2px solid #0000ff;
  border-radius: 20px;
  background-color: rgba(0, 128, 0, 0.26);
}
.offer__box {
  padding: 1.5em;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}
.offer__box--content {
  margin: 1em;
}
.offer__box--title {
  text-align: center;
  font-weight: bold;
}
.offer__smallBox {
  border-radius: 20px;
  width: 100%;
}

.pricing {
  border: solid 2px #0000ff;
  border-radius: 25px;
  background-color: #e1e2e3;
}
.pricing__article {
  padding: 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.pricing__article--title {
  text-align: center;
  font-weight: bold;
  color: #0000ff;
  padding: 1em;
}
.pricing__article--content {
  text-align: center;
}

.calc {
  padding: 80px 0;
  background-color: #F7F7F7;
}
@media only screen and (max-width: 795px) {
  .calc {
    padding: 30px 15px;
  }
}
.calc h2 {
  text-align: center;
}
.calc__columns {
  max-width: 865px;
  margin: 50px auto 0;
  display: flex;
}
@media only screen and (max-width: 795px) {
  .calc__columns {
    display: block;
  }
}
.calc__form {
  width: 50%;
  margin-right: 20px;
}
@media only screen and (max-width: 795px) {
  .calc__form {
    width: 100%;
    margin-right: 0;
  }
}
.calc__input input, .calc__select .select__input {
  color: #818181;
  width: 100%;
  border-radius: 6px;
  border: 1px solid #0000ff;
  padding: 11px 20px;
  line-height: 1em;
  font-size: 0.8125rem;
  box-shadow: 0px 3px 6px rgba(0, 128, 0, 0.26);
  margin-bottom: 20px;
  background-color: #ffffff;
}
.calc__input input::placeholder {
  color: #818181;
}
.calc__select {
  position: relative;
}
.calc__select .select__input {
  position: relative;
  height: 40px;
  vertical-align: middle;
  padding: 13px 20px;
  cursor: pointer;
}
.calc__select .select__input::after {
  content: "";
  background-image: url("../assets/Arrow.svg");
  width: 22px;
  height: 22px;
  display: block;
  position: absolute;
  right: 14px;
  top: 9px;
}
.calc__select .select__dropdown {
  display: none;
  position: absolute;
  z-index: 1;
  background-color: white;
  width: 100%;
  border-radius: 6px;
  border: 1px solid #e1e2e3;
  overflow: hidden;
}
.calc__select .select__dropdown li {
  font-size: 13px;
  padding: 10px 20px;
  cursor: pointer;
}
.calc__select .select__dropdown li:hover {
  background-color: #F7F7F7;
}
.calc__select.open .select__input::after {
  transform: rotate(180deg);
}
.calc__select.open .select__dropdown {
  display: block;
}
.calc .form__checkbox .consent__text {
  padding-top: 4px;
}
.calc__summary {
  width: 50%;
  margin-left: 20px;
  display: flex;
  flex-direction: column;
}
@media only screen and (max-width: 795px) {
  .calc__summary {
    width: 100%;
    margin-left: 0;
    margin-top: 30px;
  }
}
.calc__summary .summary__total,
.calc__summary .list__item {
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  border-radius: 6px;
  justify-content: space-between;
  align-items: center;
  color: #ffffff;
  font-size: 0.875rem;
  display: none;
}
.calc__summary .summary__total.open,
.calc__summary .list__item.open {
  display: flex;
}
.calc__summary .summary__total {
  width: 50%;
  align-self: flex-end;
  margin-top: 22px;
  padding: 8px 20px;
  background-color: #0000ff;
}
.calc__summary .summary__total .total__price {
  font-size: 1.25rem;
  font-weight: 700;
}
.calc__summary .list__item {
  padding: 6px 20px;
  background-color: #0000ff;
  margin-bottom: 8px;
}
.calc__summary .list__item .item__price {
  font-weight: 700;
  font-size: 1.125rem;
}

